<template>
  <div class="container">
    <el-card>
      <!-- el-row
            Element UI 24rows && 24cols   
       -->
      <el-row :gutter="20">
        <el-col :span="12">
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addCateClick"></el-button>
        </el-col>
      </el-row>
      <!-- table -->
      <!-- scope-->
      <el-table :data="CateList.slice((currentPage-1)*pageSize,currentPage*pageSize)">
        <el-table-column
          prop="id"
          label="ID"
          width="180px"
          align="center">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          width="180px"
          align="center">
        </el-table-column>
        <el-table-column
          label=""
          width="270px"
          align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                icon="el-icon-edit"
                @click="handleEdit(scope.$index, scope.row.id, scope.row.name, scope.row.role)">
              </el-button>
              <el-button
                size="mini"
                type="danger"
                icon="el-icon-delete"
                @click="handleDelete(scope.$index, scope.row.id)">
              </el-button>
            </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination align='center' 
        @size-change="handleSizeChange" 
        @current-change="handleCurrentChange"
        :current-page="currentPage" 
        :page-sizes="[1,5,10,20]" 
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper" 
        :total="CateList.length">
      </el-pagination>
    </el-card>

    <!--  dialog-->
    <!-- dialogclose -->
    <el-dialog title="" :visible="dialogFormVisible" show-close @close="onClose">
      <el-form :model="newCateInfo" ref="addCateFormRef" :rules="addCateFormRules">
        <el-form-item label="" prop="name">
          <el-input v-model="newCateInfo.name" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelAddCate"> </el-button>
        <el-button type="primary" @click="confirmAddCate"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
        <el-dialog title="" :visible="dialogEditFormVisible" show-close @close="onCloseEdit">
          <el-form :model="CateInfo" ref="editCateFormRef" :rules="editCateFormRules">
            <el-form-item label="" prop="name">
              <el-input v-model="CateInfo.name" :placeholder="CateInfo.name"></el-input>
            </el-form-item>
          </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelEditCate"> </el-button>
        <el-button type="primary" @click="confirmEditCate"> </el-button>
      </div>
    </el-dialog>
    <!--   -->
  </div>
</template>

<script>
export default {
  data(){
    return {
      CateList:[],
      name:'',
      currentPage: 1, // 
      total: 10, // 
      pageSize: 10, // 

      // dialog 
      dialogFormVisible: false,
      newCateInfo: {
        name: '',
      },
      role: '1',
      addCateFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      CateInfo: {
        name: ''
      },
      dialogEditFormVisible: false,
      // IDtablescope
      toEditID: 0,
      editCateFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
        ]
      },
    }
  },
  // 
  created(){
    this.getCateList()
  },
  methods:{
    async getCateList(){
      const {data:res} = await this.$http.get('category', { 
        params:{
           pagesize:this.pageSize,
           pagenum:this.currentPage 
           } 
        })
        // console.log(res)
        // console.log(res.status)
        if (res.status != 200){
          return this.$message.error(res.message)
        }
        this.CateList = res.data
        this.total = res.total
    },
    // 
    handleSizeChange(val) {
        console.log(` ${val} `);
        this.currentPage = 1; // '/'
        this.pageSize = val; // 
    },
    // 
    handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.currentPage = val; // 
    },
    // :
    addCateClick(){
      this.dialogFormVisible = true
    },
    onClose(){
      this.dialogFormVisible = false
      this.$refs.addCateFormRef.resetFields()
    },
    // :dialog
    cancelAddCate(){
      this.dialogFormVisible = false
      // resetFieldsrules-prop
      this.$refs.addCateFormRef.resetFields()
    },
    // :dialog
    confirmAddCate(){
      this.newCateInfo.role = parseInt(this.role)
      this.$refs.addCateFormRef.validate(async (valid)=>{
        if(!valid){
          return this.$message.error('')
        }
        const {data:res} = await this.$http.post('category/add',{
          name: this.newCateInfo.name
        })
        if(res.status != 200) return this.$message.error(err.msg)
        this.$message.success('')
        this.dialogFormVisible = false
        this.getCateList()
      })
    },

    // :
    handleEdit(index, id, name, role){
      this.dialogEditFormVisible = true
      this.toEditID = id
      this.CateInfo.name = name
    },
    // d:ialog
    onCloseEdit() {
      this.dialogEditFormVisible = false
      this.$refs.editCateFormRef.resetFields()
    },
    // dialog
    cancelEditCate() {
      this.onCloseEdit()
    },
    // dialog
    confirmEditCate() {
      this.$refs.editCateFormRef.validate(async (valid)=>{
        if(!valid){
          return this.$message.error('')
        }
        const {data:res} = await this.$http.put(`category/${this.toEditID}`,{
          name: this.CateInfo.name
        })
        if(res.status != 200) return this.$message.error(err.msg)
        this.$message.success('')
        this.dialogEditFormVisible = false
        this.getCateList()
      })
    },
    // 
    async handleDelete(index, id){
      // TODO:
      const confirmRes = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
        }).catch(err => err)
      if ('cancel' === confirmRes) {// 
        return this.$message.success('')
      }
      if ('confirm' === confirmRes) { // 
        const { data : res } = await this.$http.delete(`category/${id}`)
        if (res.status !== 200) {
          return this.$message.error(res.msg)
        }
        this.$message.success('')
        //
        this.getCateList()
      }
    }
  }
}
</script>