<template>
  <div class="container">
    <el-card>
      <!-- el-row
            Element UI 24rows && 24cols   
       -->
      <el-row :gutter="20">
        <el-col :span="12">
          <!-- clearableinpout -->
          <el-input placeholder="" v-model="username" clearable @change="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addUserClick"></el-button>
        </el-col>
      </el-row>
      <!-- table -->
      <!-- scope-->
      <el-table :data="userList.slice((currentPage-1)*pageSize,currentPage*pageSize)">
        <el-table-column
          prop="ID"
          label="ID"
          width="180px"
          align="center">
        </el-table-column>
        <el-table-column
          prop="username"
          label=""
          width="180px"
          align="center">
        </el-table-column>
        <el-table-column
          label=""
          width="180px"
          align="center">
          <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <!-- ID -->
          <span style="margin-left: 10px">{{ scope.row.role == 1?'': scope.row.role == 2? '':'' }}</span>
      </template>
        </el-table-column>
        <el-table-column
          label=""
          width="270px"
          align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                icon="el-icon-edit"
                @click="handleEdit(scope.$index, scope.row.ID, scope.row.username, scope.row.role)">
              </el-button>
              <el-button
                size="mini"
                type="danger"
                icon="el-icon-delete"
                @click="handleDelete(scope.$index, scope.row.ID)">
              </el-button>
              <el-button
                size="mini"
                type="info"
                icon="el-icon-warning-outline"
                @click="onResetPwd">
              </el-button>
      </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination align='center' 
        @size-change="handleSizeChange" 
        @current-change="handleCurrentChange"
        :current-page="currentPage" 
        :page-sizes="[1,5,10,20]" 
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper" 
        :total="userList.length">
      </el-pagination>
    </el-card>

    <!--  dialog-->
    <!-- dialogclose -->
    <el-dialog title="" :visible="dialogFormVisible" show-close @close="onClose">
      <el-form :model="newUserInfo" ref="addUserFormRef" :rules="addUserFormRules">
        <el-form-item label="" prop="username">
          <el-input v-model="newUserInfo.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="newUserInfo.password" placeholder="" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="checkpass">
          <el-input type="password" v-model="newUserInfo.checkpass" placeholder="" show-password suffix-icon="el-icon-circle-check"></el-input>
        </el-form-item>
        <el-form-item label="" prop="role">
          <br>
          <el-select v-model="role" placeholder="" style="width:100%">
            <!-- value value = role -->
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelAddUser"> </el-button>
        <el-button type="primary" @click="confirmAddUser"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
        <el-dialog title="" :visible="dialogEditFormVisible" show-close @close="onCloseEdit">
      <el-form :model="userInfo" ref="editUserFormRef" :rules="editUserFormRules">
        <el-form-item label="" prop="username">
          <el-input v-model="userInfo.username" :placeholder="userInfo.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="role">
          <br>
          <el-select v-model="role" placeholder="" style="width:100%">
            <!-- value value = role -->
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelEditUser"> </el-button>
        <el-button type="primary" @click="confirmEditUser"> </el-button>
      </div>
    </el-dialog>
    <!--   -->
  </div>
</template>

<script>
export default {
  data(){
    return {
      userList:[],
      username:'',
      currentPage: 1, // 
      total: 10, // 
      pageSize: 10, // 

      // dialog 
      dialogFormVisible: false,
      newUserInfo: {
        username: '',
        password: '',
        checkpass: '',
        role: 2
      },
      role: '1',
      addUserFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 12, message: ' 4  12 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 12, message: ' 6  12 ', trigger: 'blur' }
        ],
        checkpass: [
           //
           { validator: (rule, value, callback)=>{
              if (value === '') {
                callback(new Error(''));
              } else if (value !== this.userInfo.password) {
                callback(new Error('!'));
              } else {
                callback();
              }
            }, trigger: 'blur' 
           }
        ],
      },
      // 
      userInfo: {
        username: '',
        password: '',
        checkpass: '',
        role: 2
      },
      dialogEditFormVisible: false,
      // IDtablescope
      toEditID: 0,
      editUserFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 12, message: ' 4  12 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 12, message: ' 6  12 ', trigger: 'blur' }
        ],
        checkpass: [
           //
           { validator: (rule, value, callback)=>{
              if (value === '') {
                callback(new Error(''));
              } else if (value !== this.userInfo.password) {
                callback(new Error('!'));
              } else {
                callback();
              }
            }, trigger: 'blur' 
           }
        ],
      },
    }
  },
  // 
  created(){
    this.getUserList()
  },
  methods:{
    async getUserList(){
      const {data:res} = await this.$http.get('users', { 
        params:{
           username: this.username,
           pagesize:this.pageSize,
           pagenum:this.currentPage 
           } 
        })
      // console.log(this.username)
      // console.log(res.status)
      if (res.status != 200){
        return this.$message.error(res.message)
      }
      this.userList = res.data
      this.total = res.total
    },
    // 
    handleSizeChange(val) {
        console.log(` ${val} `);
        this.currentPage = 1; // '/'
        this.pageSize = val; // 
    },
    // 
    handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.currentPage = val; // 
    },
    // :
    addUserClick(){
      this.dialogFormVisible = true
    },
    onClose(){
      this.dialogFormVisible = false
      this.$refs.addUserFormRef.resetFields()
    },
    // :dialog
    cancelAddUser(){
      this.dialogFormVisible = false
      // resetFieldsrules-prop
      this.$refs.addUserFormRef.resetFields()
    },
    // :dialog
    confirmAddUser(){
      this.newUserInfo.role = parseInt(this.role)
      this.$refs.addUserFormRef.validate(async (valid)=>{
        if(!valid){
          return this.$message.error('')
        }
        const {data:res} = await this.$http.post('user/add',{
          username: this.newUserInfo.username,
          password: this.newUserInfo.password,
          role: this.newUserInfo.role
        })
        if(res.status != 200) return this.$message.error(err.msg)
        this.$message.success('')
        this.dialogFormVisible = false
        this.getUserList()
      })
    },

    // :
    handleEdit(index, id,username, role){
      this.dialogEditFormVisible = true
      this.toEditID = id
      this.userInfo.username = username
      this.userInfo.role = role
      this.role = role.toString()
    },
    // d:ialog
    onCloseEdit() {
      this.dialogEditFormVisible = false
      this.$refs.editUserFormRef.resetFields()
    },
    // dialog
    cancelEditUser() {
      this.onCloseEdit()
    },
    // dialog
    confirmEditUser() {
      this.userInfo.role = parseInt(this.role)
      this.$refs.editUserFormRef.validate(async (valid)=>{
        if(!valid){
          return this.$message.error('')
        }
        const {data:res} = await this.$http.put(`user/${this.toEditID}`,{
          username: this.userInfo.username,
          role: this.userInfo.role
        })
        if(res.status != 200) return this.$message.error(err.msg)
        this.$message.success('')
        this.dialogEditFormVisible = false
        this.getUserList()
      })
    },
    // 
    async handleDelete(index, id){
      // TODO:
      const confirmRes = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
        }).catch(err => err)
      if ('cancel' === confirmRes) {//
        return this.$message.success('')
      }
      if ('confirm' === confirmRes) { //
        const { data : res } = await this.$http.delete(`/user/${id}`)
        if (res.status !== 200) {
          return this.$message.error(res.msg)
        }
        this.$message.success('')
        //
        this.getUserList()
    }
    },
    // 
    onResetPwd(){
      this.$message.warning('')
    }
    
  }
}
</script>