<template>
  <div class="container">
    <el-card>
      <!-- el-row
            Element UI 24rows && 24cols   
       -->
      <el-row :gutter="20">
        <el-col :span="12">
          <!-- clearableinpout -->
          <el-input placeholder="" v-model="title" clearable @change="getArticleList">
            <el-button slot="append" icon="el-icon-search" @click="getArticleList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addArticleClick"></el-button>
        </el-col>
      </el-row>
      <!-- table -->
      <!-- scope-->
      <el-table :data="ArticleList.slice((currentPage-1)*pageSize,currentPage*pageSize)">
        <el-table-column
          prop="ID"
          label="ID"
          width="50px"
          align="center">
        </el-table-column>
        <el-table-column
          prop="cid"
          label=""
          width="50px"
          align="center">
        </el-table-column>
        <el-table-column
          prop="title"
          label=""
          width="250px"
          align="center">
        </el-table-column>
        <el-table-column
          prop="desc"
          label=""
          width="250px"
          align="center">
        </el-table-column>
        <el-table-column
          prop="img"
          label=""
          width="200px"
          align="center">
          <template slot-scope="scope">
            <!-- slotsrc -->
            <img v-bind:src="scope.row.img" style="width:70px;height:70px;" alt="">
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="180px"
          align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                icon="el-icon-edit"
                @click="handleEdit(scope.$index, scope.row.ID, scope.row.Articlename, scope.row.role)">
              </el-button>
              <el-button
                size="mini"
                type="danger"
                icon="el-icon-delete"
                @click="handleDelete(scope.$index, scope.row.ID)">
              </el-button>
            </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination align='center' 
        @size-change="handleSizeChange" 
        @current-change="handleCurrentChange"
        :current-page="currentPage" 
        :page-sizes="[1,5,10,20]" 
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper" 
        :total="ArticleList.length">
      </el-pagination>
    </el-card>

  </div>
</template>

<script>
export default {
  data(){
    return {
      ArticleList:[],
      title:'', // 
      img:'',  // 
      currentPage: 1, // 
      total: 10, // 
      pageSize: 10, // 
    }
  },
  // 
  created(){
    this.getArticleList()
  },
  methods:{
    async getArticleList(){
      const {data:res} = await this.$http.get('article', { 
        params:{
           title: this.title,
           pagesize:this.pageSize,
           pagenum:this.currentPage 
        }
      })
      if (res.status != 200){
        return this.$message.error(res.message)
      }
      console.log(res)
      this.ArticleList = res.data
      this.total = res.total
    },
    // 
    handleSizeChange(val) {
        console.log(` ${val} `);
        this.currentPage = 1; // '/'
        this.pageSize = val; // 
    },
    // 
    handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.currentPage = val; // 
    },
    // :
    addArticleClick(){
       this.$router.push('/artadd')
    },

    // :
    handleEdit(index, id,Articlename, role) {
      this.$router.push(`/artadd/${id}`)
    },

    // 
    async handleDelete(index, id) {
      // TODO:
      const confirmRes = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
        }).catch(err => err)
      if ('cancel' === confirmRes) {//
        return this.$message.success('')
      }
      if ('confirm' === confirmRes) { //
        const { data : res } = await this.$http.delete(`article/${id}`)
        if (res.status !== 200) {
          return this.$message.error(res.msg)
        }
        this.$message.success('')
        //
        this.getArticleList()
      }
    },    
  }
}
</script>