<template>
  <div>
    <el-card>
      <h3>{{id?'':''}}</h3>
      <el-form :model="artInfo" :rules="formRules" ref="artInfoRef">
        <el-form-item label="" prop="title">
          <br>
          <el-input v-model="artInfo.title" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="cid">
          <br>
          <!-- , -->
          <el-select v-model="cid" :placeholder="cidName? cidName:''" clearable style="width: 500px;" @change="optionChange">
            <el-option
              v-for="item in cateList"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <el-input type="textarea" v-model="artInfo.desc" placeholder="150"></el-input>
        </el-form-item>
        <el-form-item label="" prop="img">
          <!-- TODO:  -->
          <br>
          <!-- 'File' -->
          <el-upload
            ref='upLoadRef'
            class="upload-demo"
            drag
            :limit="1"
            accept="image/jpeg,image/gif,image/png"
            list-type="picture"
            name='File'
            :action=upRUL
            :headers=headers
            :on-preview="handleOnPreview"
            :on-success="handleSuccess"
            :on-error="handleError"
            >
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><em></em></div>
            <div class="el-upload__tip" slot="tip">
              <i class="el-icon-warning" style="color:red;"></i>
              jpg/png500kb
            </div>
            
          </el-upload>
          <!--  -->
          <template v-if="id">
            <img :src="artInfo.img" style="width:120px;height:100px;" alt="" />
          </template>
        </el-form-item>
        <el-form-item label="" prop="content">
          <br>
          <Editor v-model="artInfo.content" placeholder=""></Editor>
        </el-form-item>
        <el-form-item class="loginBtn">
          <el-button type="primary" @click="submitArt(artInfo.id)">{{artInfo.id?'':''}}</el-button>
          <el-button @click="cancelSubmit"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog :visible.sync="showPreview" center width="40%">
        <el-image :src="artInfo.img" fit="fill" alt=""></el-image>
    </el-dialog>
  </div>
</template>

<script>
import {URL} from '../../plugin/http'
import Editor from '../editor/index.vue'
export default {
  components:{ Editor},
  // 
  props:['id'],
  data(){
    return{
      artInfo:{
        id:0,
        title:'',
        cid:0,
        desc:'',
        content:'',
        img:''
      },
      cid:'',
      cidName:'',
      cateList:[],
      selectPlh:'',
      upRUL: URL + '/upload', // api
      headers:{},
      showPreview: false,
      formRules:{
        title:[{ required: true, message: '', trigger: 'blur' },
               { min: 1, max: 35, message: ' 1  35 ', trigger: 'blur' }],
        cid:  [{ required: true, message: '', trigger: 'blur' }],
        desc: [{min:0, max: 150, message: '150', trigger: 'blur' }],
        content:[{ required: true, message: '', trigger: 'blur' }],
        img:[{ required: true, message: '', trigger: 'blur'}]
      }
    }
  },
  created(){
    // 
    this.getCateList()
    // JWTtoken
    this.headers = {Authorization: `Bearer ${window.sessionStorage.getItem('token')}`}
    if(this.id){
      this.getArtInfo(this.id)
    }
  },
  methods:{
    async getArtInfo(id){
      const {data:res} = await this.$http.get(`article/info/${id}`)
      if(res.status != 200) return this.$message.error('')
      this.artInfo = res.data
      this.artInfo.id = res.data.ID
      this.cidName = res.data.Category.name
      // console.log(res.data)
      this.$message.success('')
    },
    async getArticleList(){
      const {data:res} = await this.$http.get('article', { 
        params:{
           title: this.title,
           pagesize:this.pageSize,
           pagenum:this.currentPage 
        }
      })
      if (res.status != 200){
        return this.$message.error(res.message)
      }
      console.log(res)
      this.ArticleList = res.data
      this.total = res.total
    },
    // 
    async getCateList() {
      const {data:res} = await this.$http.get('category')
      if(res.status != 200) return this.$message.error('')
      this.cateList = res.data
    },
    // 
    optionChange(value){
      this.artInfo.cid = value
    },

    handleOnPreview(){
      this.showPreview = true
    },
    // 
    // on-chage
    // 
    // 
    handleSuccess(response, file, fileList){
      let status = response.status
      if(status != 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      // upload
      this.artInfo.img = response.url
    },
    handleError(){
      this.$message.error('')
    },
    // 
    submitArt(id){
      this.$refs.artInfoRef.validate(async(valid)=>{
        if(id === 0){
          const {data:res} = await this.$http.post('article/add',this.artInfo)
          if(res.status !== 200){
            return this.$message.error(res.message)
          }
          this.$message.success('')
          this.$router.push('/artlist')
        } else {
          const {data:res} = await this.$http.put(`article/${id}`,this.artInfo)
          if(res.status !== 200){
            return this.$message.error(res.message)
          }
          this.$message.success('')
          this.$router.push('/artlist')
        }
      })
    },
    cancelSubmit(){
      this.$refs.artInfoRef.resetFields()
    },
  }
}
</script>

